* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    background-color: white;
}

textarea {
    resize: none;
    width: 320px;
    height: 250px;
    border: none;
    box-shadow: 0px;
    outline: none;
    margin-top: 30px;
}

textarea:focus, textarea:hover {
    border: none;
    box-shadow: 0px;
    padding-right: 20px;
}

svg {
    transform: rotate(90deg);
    vertical-align: middle;
    width: 16px;
    height: 16px;
    fill: #666;
}

.title {
    color: #222;
    font-size: 16px;
    font-family: sans-serif;    
}   

.encoder {
    display: inline-flex;
    padding: 20px;
    border: 1px solid gray;
    box-shadow: 0 2px 3px rgb(0 0 0 / 6%);
    border: 1px solid rgba(150,150,150,0.3);
}

.title-container {
    display: flex;
}

.special-for-khachik {
    position: relative;
    font-size: 14px;
    font-family: sans-serif;
    left: 30%;
    top: 120px;
}

.app {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}